import { DATE_FORMAT_STRING } from "@/config";
import * as dailyReportService from "@/service/daily-report";
import { format } from "date-fns";

const state = () => ({
    form: { fromDate: new Date(), toDate: new Date(), selectedDoctors: [] },
    bhcgForm: { fromDate: new Date(), toDate: new Date(), selectedDoctors: [] },
    reportList: [],
    selectedReport: null,
    billingList: [],
    selectedDoctors: [],
    bhcgReportList: [],
    isDailyReport: false
});

const actions = {
    async getTotalPatient(context, fromDate, toDate) {
        try {
            const data = await dailyReportService.getTotalPatient(
                fromDate,
                toDate
            );
            return data;
        } catch (err) {
            Promise.reject();
        }
    },
    async getTotalOrders(context, fromDate, toDate) {
        try {
            const data = await dailyReportService.getTotalOrders(
                fromDate,
                toDate
            );
            return data;
        } catch (err) {
            Promise.reject();
        }
    },
    async getCycleData(context, autoTestOrderId) {
        try {
            const { data } = await dailyReportService.getCycleData(
                autoTestOrderId
            );
            return data;
        } catch (err) {
            Promise.reject();
        }
    },
    async assignCycleONUnassignedTests(
        { state, rootState },
        { fromDate, toDate }
    ) {
        try {
            const data = await dailyReportService.assignCycleONUnassignedTests({
                fromDate: format(state.form.fromDate, DATE_FORMAT_STRING),
                toDate: format(state.form.toDate, DATE_FORMAT_STRING)
            });
            return data;
        } catch (error) {
            return Promise.reject();
        }
    },
    async getOrderList({ state, rootState }, { fromDate, toDate, providerNo }) {
        if (state.isDailyReport) {
            try {
                const data = await dailyReportService.getOrderList(
                    format(state.form.fromDate, DATE_FORMAT_STRING),
                    format(state.form.toDate, DATE_FORMAT_STRING)
                    // rootState.artCycle.selectedCycle.patientId
                );
                state.reportList = data;
                state.selectedReport = data[0];
                return Promise.resolve();
            } catch (error) {
                state.reportList = [];
                state.selectedReport = null;
                return Promise.reject();
            }
        } else {
            try {
                const data = await dailyReportService.getBhCGList(
                    format(state.bhcgForm.fromDate, DATE_FORMAT_STRING),
                    format(state.bhcgForm.toDate, DATE_FORMAT_STRING),
                    rootState.artCycle.selectedCycle.patientId
                );
                state.bhcgReportList = data;
                state.selectedReport = data[0];
                return Promise.resolve();
            } catch (error) {
                state.bhcgReportList = [];
                state.selectedReport = null;
                return Promise.reject();
            }
        }
    },

    async getPendingOrderList(context, fromDate, toDate, providerNo) {
        const data = await dailyReportService.getOrderList(
            fromDate,
            toDate,
            providerNo
        );
        return data;
    },
    async assignCycleToBloodTest({ state }, id) {
        try {
            const data = await dailyReportService.assignCycleToBloodTest({
                cycleId: id,
                testId: state.selectedReport.autoTestOrderId
            });
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async deassignCycleToBloodTest({ state }, id) {
        try {
            const data = await dailyReportService.deassignCycleToBloodTest(id);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },
    async getPersonalCycles({ state }) {
        try {
            const {
                personalCycles
            } = await dailyReportService.getPersonalCycle(
                state.selectedReport.autoTestOrderId
            );
            return personalCycles;
        } catch (error) {
            return Promise.reject();
        }
    },
    async saveCheckedSigleRecord({ state }, payload) {
        try {
            const data = await dailyReportService.saveCheckedSigleRecord(
                payload
            );
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async saveCheckDefault({ rootState, state, dispatch }) {
        if (state.selectedReport == null) return;
        let report = state.selectedReport;

        // todo provider no check

        try {
            await dispatch("saveCheckedSigleRecord", {
                key: report.autoTestOrderId,
                value: true,
                checknum: 2,
                currentUser: rootState.user.username
            });

            report.checked2 = true;
            state.selectedReport = report;
        } catch (err) {}
    },
    async getFundedBloodBillingList({ state }, params) {
        try {
            const data = await dailyReportService.getFundedBloodBillingList(
                params
            );
            state.billingList = data;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getBloodBillingList({ state }, params) {
        try {
            const data = await dailyReportService.getBloodBillingList(params);
            state.billingList = data;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async pushToOscar({ state }, payload) {
        try {
            const data = await dailyReportService.pushToOscar(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },
    async pushAllToOscar({ state }, payload) {
        try {
            const data = await dailyReportService.pushAllToOscar(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    }
};

const mutations = {
    SET_SELECTED_REPORT(state, payload) {
        state.selectedReport = payload;
    },
    SET_SELECTED_DOCTORS(state, payload) {
        if (state.isDailyReport) {
            state.form.selectedDoctors = [...payload];
        } else {
            state.bhcgForm.selectedDoctors = [...payload];
        }
    },
    SET_IS_DAILY_REPORT(state, payload) {
        state.isDailyReport = payload;
    }
};

const getters = {
    isAnyReportSelected(state) {
        return state.selectedReport != null ? true : false;
    },
    getReportList(state) {
        return state.reportList.filter((item) => {
            return state.selectedDoctors.includes(item.drNum);
        });
    },
    getCurrentDoctorCheckedReportList(state, _, rootState) {
        return state.reportList.filter(
            (item) => item.checked && item.drNum === rootState.user.providerNo
        );
    },
    getReport(state) {
        if (state.isDailyReport) return state.reportList;
        else return state.bhcgReportList;
    },
    getForm(state) {
        if (state.isDailyReport) return state.form;
        else return state.bhcgForm;
    },
    getSelectedDoctors(state) {
        if (state.isDailyReport) return state.form.selectedDoctors;
        else return state.bhcgForm.selectedDoctors;
    }
};

export default {
    namespaced: true,
    actions,
    state,
    mutations,
    getters
};
